import angularAMD from 'angularAMD';
import "./theme/dark";
import "./theme/shine";

angularAMD.factory('echartsTheme', ['darkTheme', 'shineTheme',
	(dark,shine) => {
    let themes = {
        dark: dark, 
        shine:shine, 
    };
    return {
        get: function(name) {
            return themes[name] ? themes[name] : {};
        },
    };
}]);
